
import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push } from 'react-router-redux';


import './Login.css';
import Loader from '../../../common/Components/Loader'

import agent from '../../../agent';

import { store } from '../../../store';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: {
        username: '',
        password: '',
      },
      loading: false,
      error:""
    };
  }

  login(e) {
    e.preventDefault();
    this.setState({loading:true,error:""})
    const data = this.state.user;
    agent.Auth.login(data).then((res) => {
      console.log(res);
      agent.setToken(res.data.token)
      window.localStorage.setItem('bearer', res.data.token);
      store.dispatch(push('/'));
    this.setState({loading:false})

    }).catch((err) => {
      console.log(err);
    this.setState({loading:false})

    if (err.response) {
      console.log(err.response);
      this.setState({ error: err.response.data.error,loading:false });
    }
    });
  }

  render() {
    const { username, password } = this.state.user;
    return (
      <div>
        <div className=" ba tc w-100 pa4 box vertical-align " style={{  position: 'relative' }}>


          <h1 className=" heading center">Instagram</h1>
          <form onSubmit={this.login.bind(this)}>


            <div className="input-label-div">
              <input
                type="text"
                name="phone-number"
                className="inp"
                value={username}
                onChange={(e) => this.setState({ user: { ...this.state.user, username: e.target.value } })}
                required
              />
              <label className="inp-label">
                Email
              </label>
            </div>
            <div className="input-label-div">
              <input
                type="password"
                name="phone-number"
                className="inp"
                required
                value={password}
                onChange={(e) => this.setState({ user: { ...this.state.user, password: e.target.value } })}
              />
              <label className="inp-label">
                        password
              </label>
            </div>
            <p className="err-txt">{this.state.error}</p>
            <input type="submit" value="Login" className="color-btn" />
          </form>
          <button className="btn" type="button">
                Forgot password

          </button>
        </div>
        <div className=" tc box  mt3" type="button">
          <button onClick={() => this.props.changeScreen()} className="btn" type="button">
            <p>
                Don't have an account?
              <a><span>Sign up</span></a>
            </p>

          </button>
        </div>
        {
        this.state.loading
          ? (
            <Loader/>
          ) : null
            }
      </div>
    );
  }
}


const mapStateToProps = (state) => ({

});

const mapDispatchToProps = (dispatch) => ({

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Login);
